<template>
   <div class="item" @click="Expand" ref="showcase-item">
      <img :src="imageSrc" v-bind:alt="item.image" ref="image"/>
      <transition name="fade">
         <div class="label" ref="label" v-if="!showingInfo">
            <h3>{{ item.name }}</h3>
            <p class="tagline">{{ item.tagline }}</p>
         </div>
      </transition>
      <transition name="fade">
         <div class="expanded-content" v-if="expanded">
            <div class="awards" ref="awards" v-if="item.awards != null">
               <div v-for="award in item.awards" :key="award.name" class="award">
                  <svg class="award-icon" v-bind:style="{fill: award.color}" xmlns="http://www.w3.org/2000/svg" width="17" height="20" viewBox="0 0 24 24" fill="none" stroke="#ffffff" stroke-width="1.75" stroke-linecap="round" stroke-linejoin="round"><polygon points="12 2 15.09 8.26 22 9.27 17 14.14 18.18 21.02 12 17.77 5.82 21.02 7 14.14 2 9.27 8.91 8.26 12 2"></polygon></svg>
                  <p>{{award.award}}</p>
               </div>
            </div>
            <div class="links" ref="links">
               <div v-for="link in item.links" :key="link.link" class="link">
                  <a :href="link.link" target="_blank">
                     <img :src="link.icon"/>
                  </a>
               </div>
            </div>
            <div class="info-text" v-on:click="ToggleInfo()" ref="info-text" v-if="expanded">
               info
            </div>
         </div>
      </transition>

      <transition name="fade">
         <div class="info-box" ref="info-box" v-if="showingInfo">
            <p>{{item.info}}</p>
         </div>
      </transition>
   </div>
</template>

<script>
export default {
   name: "Showcase-Item",
   props: {
      item: Object,
      imageSrc: String
   },
   data() {
      return {
         hover: false,
         showingInfo: false,
         expanded: false
      }
   },
   methods: {
      ToggleInfo() {
         // switches between showing the info box & the label title

         this.$refs["info-text"].innerHTML = this.showingInfo ? "info" : "back";
         this.showingInfo = !this.showingInfo;
      },
      Collapse() {
         // collapses the entire showcase item, making it smaller & removing things like the awards & links

         if(!this.expanded)
            return;

         this.showingInfo = false;
         this.expanded = false;

         this.$refs["showcase-item"].classList.remove("selected-showcase-item");
      },
      Expand() {
         // expands showcase item, making awards & links visible

         if(this.expanded)
            return;

         this.$emit("expand");
         this.expanded = true;

         this.$refs["showcase-item"].classList.add("selected-showcase-item");
      }
   }
}
</script>

<style scoped>
.item {
   display: flex;
   justify-content: center;
   align-items: center;
   flex-basis: 100%;

   transition: 0.3s;
   -webkit-transition: 0.3s;
   -moz-transition: 0.3s;
   width: 100%;
   height: 130px;
   margin: 6px 0 6px 0;

   backface-visibility: hidden;
   -webkit-backface-visibility: hidden;
   -moz-backface-visibility: hidden;
   transform: translate3d(0, 0, 0);
   -webkit-transform: translate3d(0, 0, 0);
   -moz-transform: translate3d(0, 0, 0)
}

.item:hover {
   cursor: pointer;
}

.item:hover .label .tagline, .selected-showcase-item .label .tagline {
   opacity: 1;
   height: 16px;
}

.item:hover img {
   filter: saturate(100%)
}

.item img {
   height: 100%;
   width: 100%;
   object-fit: cover;
   border-radius: 10px;
   filter: saturate(50%);
   transition: 0.3s;
   -webkit-transition: 0.3s;
   -moz-transition: 0.3s;
}

.expanded-content {
   position: fixed;
   width: 100%;
   height: 100%;
}

.label {
   position: fixed;
   text-align: center;
   background-color: rgba(36, 36, 36, 0.80);
   padding: 6px 10px;
   border-radius: 8px;
   color: white;
   width: auto;
   display: inline-block;
   transition: 0.3s;
   -webkit-transition: 0.3s;
   -moz-transition: 0.3s;
}

.label h3 {
   font-size: 17px;
   font-weight: 500;
}

.label p {
   font-size: 12px;
   transition: 0.3s;
   -webkit-transition: 0.3s;
   -moz-transition: 0.3s;
   height: 0px;
}

.tagline {
   font-size: 12px;
   color: lightgray;
   opacity: 0;
   transition: 0.3s;
   -webkit-transition: 0.3s;
   -moz-transition: 0.3s;
}

.selected-showcase-item {
   height: 393px;
   cursor: auto !important;
}

.selected-showcase-item img {
   filter: saturate(100%)
}

.hidden {
   opacity: 0;
   pointer-events: none;  
}

.awards {
   position: fixed;
   top: 3%;
   left: 2%;
   padding: 3.9px 7px 3.9px 3.9px;
   transition: 0.3s;
   -webkit-transition: 0.3s;
   -moz-transition: 0.3s;
   background-color: rgba(36, 36, 36, 0.60);
   border-radius: 10px;
}

.award {
   display: flex;
   flex-direction: row;
   transition: 0.3s;
   -webkit-transition: 0.3s;
   -moz-transition: 0.3s;
}

.award p {
   color: white;
   font-size: 13px;
}

.award-icon {
   padding-right: 3px;
}

.links {
   position: fixed;
   bottom: 3%;
   left: 50%;
   transform: translateX(-50%);
   display: flex;
   flex-direction: row;
   align-items: center;
   transition: 0.3s;
   -webkit-transition: 0.3s;
   -moz-transition: 0.3s;
   background-color: rgba(36, 36, 36, 0.60);
   border-radius: 10px;
   padding: 3px 3px 0 3px;
}

.link {
   width: 32px;
   height: auto;
   padding: 3px 3px 0 3px;
   transition: 0.1s;
   -webkit-transition: 0.1s;
   -moz-transition: 0.1s;
}

.link:hover {
   opacity: 0.5;
   cursor: pointer;
}

.info-text {
   position: fixed;
   top: 3%;
   right: 2%;

   transition: 0.3s;
   -webkit-transition: 0.3s;
   -moz-transition: 0.3s;

   background-color: rgba(36, 36, 36, 0.60);
   border-radius: 10px;
   padding: 5px;

   text-align: center;

   cursor: pointer;

   font-size: 12px;
   color: lightgray;
}

.info-text:hover {
   background-color: rgba(24, 24, 24, 0.7);
   color: white;
}

.info-box {
   position: fixed;
   width: 50%;
   height: 50%;
   background-color: rgba(36, 36, 36, 0.60);
   border-radius: 10px;
   padding: 21px;

   transition: 0.3s;
   -webkit-transition: 0.3s;
   -moz-transition: 0.3s;
}

.info-box p {
   color: white;
}

.fade-enter-active, .fade-leave-active {
  transition: opacity .3s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
</style>
